import { login, register } from "@/api/auth";
const Cookie = process.client ? require("js-cookie") : undefined;

export default {
  name: "Login",
  middleware: ["notAuth"],
  // async asyncData(context) {
  //   // console.log(context);
  //   const { route } = context;
  //   const isLogin = route.path === "/login";

  //   return {
  //     isLogin
  //   };
  // }
  data() {
    return {
      user: {
        username: "",
        email: "",
        password: ""
      },
      errmsg: []
    };
  },
  computed: {
    isLogin() {
      return this.$route.name === "login";
    },
    title() {
      return this.$route.name === "login" ? "Sign In" : "Sign Up";
    }
  },
  methods: {
    async handleSubmit() {
      const data = {
        user: this.user
      };
      const submitHandler = this.isLogin ? login : register;

      try {
        const res = await submitHandler(data);

        this.$store.commit("setUser", res.user);
        Cookie.set("user", res.user); // saving token in cookie for server rendering
        this.$router.push("/");
      } catch (error) {
        // console.log(error);
        const { errors } = error;
        if (errors) {
          this.errmsg = Object.keys(errors).reduce((pre, key) => {
            errors[key].forEach(desc => {
              pre.push(`${key} ${desc}`);
            });
            return pre;
          }, []);
        }
      }
    }
  }
};
